html {
    box-sizing: border-box;
  }
  
  *,
  *::before,
  *::after {
    box-sizing: inherit;
  }
  
  body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
      Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif,
      'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
  }
  
  .timer {
    display: flex;
    justify-content: center;
    padding-top: 50px;
    font-size: 24px;
    font-weight: 700;
    padding-bottom: 50px;
  }
  
  .field {
    margin-right: 40px;
  }
  
  .btn-wrap {
    display: flex;
    justify-content: center;
  }
  
  .btn {
    display: block;
    margin-right: 25px;
    cursor: pointer;
    width: 200px;
    padding-top: 10px;
    padding-bottom: 10px;

    &[data-action="start"] {
        background-color: green;
    }

    &[data-action="stop"] {
        background-color: red;
    }
  }
  
  .wrap-calendar {
    text-align: center;
    font-size: 40px;
  }
  
  .calendar {
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 50px;
  }